import React from 'react';
import './header.css';

const header = (props) => {
    const logoUrl = require('../../assets/icons/logo.svg');

    return (
        <div className='header' >
        <a className='link' id='dashboardLink' href={props.dashboardUrl}>&#60; Revenir au tableau de bord</a>
        <div className='headerContainer'>
                <img src={logoUrl} id='logo' alt='logo' />
                <h1 className='brand'>COURS MASSON</h1>
            <h2 className='title'>
                Tableau blanc interactif pour cours particuliers
            </h2>
            <p className='paragraph'>
                Les tableaux blancs interactifs intgrent une fonctionalit de visio-confrence. 
                Ils sont conus pour fournir une exprience pdagogique optimal pour donner un cours particulier en ligne.  
            </p>

            <button className='btn ctaBtn' onClick={props.create} >Crer un tableau blanc</button>
            <p className='subtitle'>Les tableaux blancs ont une dure de vie 24 heures</p>
            </div>
        </div>
    );
}

export default header;